<template>
  <div class="row">
    <Login v-if="dom.login"/>
    <loading :active.sync="isLoading">
      <img alt="Meason" src="../../assets/img/loading.svg" width="100">
    </loading>
    <div class="col-12">
      <div class="user-title">
        <h2>
          <i class="fas fa-compact-disc"></i>
           我的作品
          <span v-if="tempDom.deleteOpen"> - 管理模式</span></h2>
        <button type="button" class="btn btn-outline-primary btn-sm"
          v-if="!tempDom.deleteOpen"
          @click="tempDom.deleteOpen = true">管理音樂</button>
        <button type="button" class="btn btn-outline-dark btn-sm"
          v-else
          @click="tempDom.deleteOpen = false">退出管理模式</button>
      </div>
    </div>
    <div class="col-lg-4 col-md-6 col-sm-4"
      v-for="(item, index) in songList" :key="index">
      <a class="song-likes" href="#"
        v-if="userData"
        title="加入最愛"
        @click.prevent="liked(item)"
        v-html="comLike(item.like)"></a>
      <a class="song-likes" href="#"
        v-else title="加入最愛"
        @click.prevent="dom.login = true;dom.opened = false">
        <i class="far fa-heart"></i>
      </a>
      <div class="music-con">
        <div class="photo"
          @click="togglePlay(index)"
          :class="{'playing': player.id === item.id}"
          :style="{'backgroundImage': `url(${item.cover})`}">
          <img src="../../assets/img/bg/scale.png" width="100%" alt="">
          <button class="click"><i class="far fa-play-circle"></i></button>
          <p class="views">{{item.view}} <small>瀏覽</small></p>
        </div>
        <div class="text">
          <router-link :to="`/music/${item.id}`" tag="h3">{{item.name}}</router-link>
          <h4 class="type">
            <i class="fas fa-music"></i>
            <p class="type"><span v-for="i in item.type" :key="i">{{i}}</span></p>
          </h4> 
        </div>
        <div class="artist">
          <router-link to="/user" :title="item.artist">
            <i class="fas fa-user"></i> {{item.artist}}
          </router-link>
          <span v-if="item.like">{{item.like.length}} 收藏</span>
        </div>
        <ul class="edit-con animated fadeInUp"
          v-if="tempDom.deleteOpen">
          <li><router-link :to="`music-editor/${item.id}`">編輯</router-link></li>
          <li @click="deleteMusic(item)">刪除</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase';
import Login from '../../components/Login.vue'

export default {
  name: 'UserPorfile',
  props: ['userData'],
  data() {
    return {
      isLoading: false,
      dom:{
        login: false,
        opened: false,
      },
      player: {
        id: '',
        pause: false,
      },
      songList: [],
      tempDom: {
        deleteOpen: false
      }
    }
  },
  components:{
    Login
  },
  methods: {
    togglePlay(index) {
      const vm = this;
      let newSongList = []; // 複製一個 Arr 用
      if(!vm.player.pause || vm.songList[index].id!==vm.player.id) {
        vm.songList.forEach(i => {
          newSongList.push(i)
        });
        newSongList.unshift(vm.songList[index]);
        newSongList.splice(index + 1, 1);
        this.$bus.$emit('audio:list', newSongList);
        vm.player.pause = true;
        // vm.player.id = vm.songList[index].id;
      } else {
        this.$bus.$emit('audio:pause');
        vm.player.pause = false;
        vm.player.id = '';
      }      
    },
    getMusicData() {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/getPostData`;
      const reqURL = `users/${vm.userData.uid}/music/`;
      vm.isLoading = true;
      vm.$http.post(url, {"url": reqURL}).then((response) => {
        vm.songList = response.data;
        vm.isLoading = false;
      })
      .then((result) => {
        vm.songList.forEach(val => {
          if(val.like) {val.like = Object.keys(val.like); }         
        });
      })
      .then(result => {
        vm.songList.forEach(val => {
          if(val.type) {val.type = Object.values(val.type); }         
        });
      })
      .catch(e => {
        vm.isLoading = false;
      })

    },
    comLike(item) {
      const vm = this;
      const user = vm.userData.uid;
      let str = '';
      if(Array.isArray(item)){
        if(item.indexOf(user) !== -1){
          str = '<i class="fas fa-heart"></i>'
        } else {
          str = '<i class="far fa-heart"></i>'
        }
      } else {
        str = '<i class="far fa-heart"></i>'
      }
      return str
    },
    liked(item) {
      // 按讚
      const vm = this;
      const user = vm.userData.uid;
      if(vm.userData) {
        if(item.like) {
          if(item.like.indexOf(user) !== -1) {
            vm.sendLikeAPI(item, 'unlike')
          } else {
            vm.sendLikeAPI(item, 'likes')
          }          
        } else {
          vm.sendLikeAPI(item, 'likes')
        }
      }      
    },
    sendLikeAPI(item, method) {
      // 按讚 API
      const vm = this;
      const url = `${process.env.VUE_APP_API}/likeMusic`;
      const reqData = {
        method: method,
        userId: vm.userData.uid,
        musicId: item.id,
        posterId: item.poster_uid,
      }
      vm.$http.post(url, {'reqData':reqData}).then((response) => {
        if(response.data.success){
          vm.getMusicData();
        }
      })
    },
    deleteMusic(item) {
      const vm = this;
      const r = confirm("確定要刪除?");
      const url = `${process.env.VUE_APP_API}/deleteMusic`;
      const storageRef = firebase.storage().ref('music/' + item.fileName); //資料夾+黨名
      const reqData = {
        userId: vm.userData.uid,
        musicId: item.id,
        posterId: item.poster_uid,
      }
      if (r == true) {
        vm.$http.post(url, {'reqData':reqData}).then((response) => {
          if(response.data.success){
            storageRef.delete().then(function() {
              vm.getMusicData();
            }).catch(function(error) {
              alert('刪除中發生錯誤，請嘗試重新整理後重試')
            });
          }
        })
      }
    }
  },
  created() {
    const vm = this;
    vm.getMusicData()
    vm.$bus.$on('current:id',(id = '') => {
      vm.player.id = id;
      // vm.player.view += 1;
    })
    vm.$bus.$on('close:login',(val) => {
      vm.dom.login = val;
    })
  },
}
</script>

<style lang="scss" scoped>
.aplayer.aplayer-narrow{
  width: 100%;
  height: 150px;
  .aplayer-pic{
    width: 100%;
    height: 150px;
  }
}
.edit-con{
  background-color: #fecd88;
  margin-bottom: 0;
  text-align: center;
  color: #000;
  font-weight: bold;
  margin-top: 5px;
  list-style: none;
  padding: 0;
  display: flex;
  li{
    cursor: pointer;
    margin-top: 8px;
    flex: 1;
    box-sizing: border-box;
    border-left: 1px solid rgba(#000, .3);
    transition: color 0.2s ease-in-out;
    a{
      color: #000;
    }
    &:first-child{
      border: none;
    }
    &:hover{
      color: #555;
    }
  }
}
</style>
